<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left">
                <div class="post__box">
                    <h1>Post information</h1>   
                    <h2>Post title:{{ getPost.title }}</h2>
                    <p>Post body:{{ getPost.body }}</p>
                </div>     
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return{
               currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
            this.getPosts()
        },
        computed: {
            posts() {
                return this.$store.state.posts
            },
            getPost() {
                    return this.posts.find((info) => info.id == this.currentId)
            }
        },
        methods: {
            getPosts() {
                this.$store.dispatch('getPosts')
            }
        }
}
</script>
